import React, { useEffect } from "react";
import { LOGIN_ROUTE, MAIN_ROUTE, REG_ROUTE } from "../utils/consts";
import { useLocation, useNavigate, NavLink } from "react-router-dom";
import "../styles/auth.css";

const Auth = () => {
  const location = useLocation();
  const navigate = useNavigate();
  const isLogin = location.pathname === LOGIN_ROUTE;

  useEffect(() => {
    //        
    const authContainer = document.getElementById("auth-container");
    if (isLogin) {
      authContainer.classList.remove("active");
    } else {
      authContainer.classList.add("active");
    }
  }, [isLogin]);

  const handleSignUp = (event) => {
    event.preventDefault();
    const userName = event.target[0].value; // 
    const userSurname = event.target[1].value; // 
    const userEmail = event.target[2].value; // 
    const userPassword = event.target[3].value; // 

    //   
    console.log({ userName, userSurname, userEmail, userPassword });
    //       
    navigate(LOGIN_ROUTE);
  };

  const handleSignIn = (event) => {
    event.preventDefault();
    const userEmail = event.target[0].value; // 
    const userPassword = event.target[1].value; // 

    //   
    console.log({ userEmail, userPassword });
    //      
    navigate(REG_ROUTE); //    
  };

  const handleRegisterClick = () => {
    navigate(REG_ROUTE);
  };

  const handleLoginClick = () => {
    navigate(LOGIN_ROUTE);
  };

  return (
    <div className="authorization-container">
      <div className="auth-container" id="auth-container">
        <div className={`form-container sign-up ${!isLogin ? "active" : ""}`}>
          <form onSubmit={handleSignUp}>
            <h1 className="auth-h1"> </h1>
            <input type="text" placeholder="" required />
            <input type="text" placeholder="" required />
            <input type="email" placeholder="" required />
            <input type="password" placeholder="" required />
            <button type="submit"></button>
          </form>
        </div>
        <div className={`form-container sign-in ${isLogin ? "active" : ""}`}>
          <form onSubmit={handleSignIn}>
            <h1 className="auth-h1"></h1>
            <input type="email" placeholder="" required />
            <input type="password" placeholder="" required />
            <button type="submit"></button>
          </form>
        </div>
        <div className="toggle-container">
          <div className="toggle">
            <div className="toggle-panel toggle-left">
              <h1 className="auth-h1"> !</h1>
              <p>
                   ,    
                
              </p>
              <button className="hidden" onClick={handleLoginClick}>
                
              </button>
              <NavLink to={MAIN_ROUTE} className="exit-link">
                
              </NavLink>
            </div>
            <div className="toggle-panel toggle-right">
              <h1 className="auth-h1">, !</h1>
              <p>,     </p>
              <button className="hidden" onClick={handleRegisterClick}>
                
              </button>
              <NavLink to={MAIN_ROUTE} className="exit-link">
                
              </NavLink>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Auth;
